<template>
<div :class="$style.selection">
  <ul :class='["u-clearfix", $style.drama, episodes.length <= 13 ? $style.line: ""]' v-if='bizType == 1 && episodes.length <= 50'>
    <template v-if='episodes.length <= lineNum * 2'>
      <li :class='$style.item' v-for='(val, index) in episodes'>
        <a target='_blank' :href='getHref(episodes[index])'>{{episodes[index].episodeDisplay}}</a>
      </li>
    </template>
    <template v-if='episodes.length > lineNum * 2'>
      <li :class='$style.item' v-for='index in (lineNum * 2 - 2)'>
        <a target='_blank' :href='getHref(episodes[index - 1])'>{{episodes[index - 1].episodeDisplay}}</a>
      </li>
      <li :class='$style.item' v-for='index in (episodes.length - lineNum * 2 + 2)' v-show='showAll'>
        <a target='_blank' :href='getHref(episodes[index + lineNum * 2 - 3])'>{{episodes[index + lineNum * 2 - 3].episodeDisplay}}</a>
      </li>
      <li :class='$style.foldBtn' v-show='!showAll' @click='showAll = true'>??????<i :class='["iconfont", "icon-down", $style.btnArrow]'></i></li>
      <li :class='$style.foldBtn' v-show='showAll' @click='showAll = false'>??????<i :class='["iconfont", "icon-up", $style.btnArrow]'></i></li>
    </template>
  </ul>
  <ul :class='[$style.batches, "u-clearfix"]' v-if='bizType == 1 && episodes.length > 50'>
    <li :class='[$style.batch, currentBatch == index ? $style.highlight : ""]' v-for='index in (Math.ceil(episodes.length / 50))' @click='currentBatch = index'>{{(index - 1) * 50 + 1}}-{{(index) * 50}}</li>
  </ul>
  <ul :class='["u-clearfix", $style.drama]' v-if='bizType == 1 && episodes.length > 50'>
    <template v-if='currentBatches.length <= lineNum * 2'>
      <li :class='$style.item' v-for='(val, index) in currentBatches.length'>
        <a target='_blank' :href='getHref(currentBatches[index])'>{{currentBatches[index].episodeDisplay}}</a>
      </li>
    </template>
    <template v-if='currentBatches.length > lineNum * 2'>
      <li :class='$style.item' v-for='index in (lineNum * 2 - 2)'>
        <a target='_blank' :href='getHref(episodes[index - 1])'>{{episodes[index - 1].episodeDisplay}}</a>
      </li>
      <li :class='$style.item' v-for='index in (currentBatches.length - lineNum * 2 + 2)' v-show='showAll'>
        <a target='_blank' :href='getHref(episodes[index + lineNum * 2 - 3])'>{{episodes[index + lineNum * 2 - 3].episodeDisplay}}</a>
      </li>
      <li :class='$style.foldBtn' v-show='!showAll' @click='showAll = true'>??????<i :class='["iconfont", "icon-down", $style.btnArrow]'></i></li>
      <li :class='$style.foldBtn' v-show='showAll' @click='showAll = false'>??????<i :class='["iconfont", "icon-up", $style.btnArrow]'></i></li>
    </template>
  </ul>
  <ul :class='["u-clearfix", $style.variety, episodes.length <= 3 ? $style.vline: ""]' v-if='bizType == 2'>
    <template v-if='episodes.length <= 6'>
      <li :class='$style.single' v-for='(val, index) in episodes'>
        <a target='_blank' :href='getHref(val)'>{{val.episodeDisplay + "  " + val.title}}</a>
      </li>
    </template>
    <template v-if='episodes.length > 6'>
      <li :class='$style.single' v-for='(val, index) in 5'>
        <a target='_blank' :href='getHref(episodes[index])'>{{episodes[index].episodeDisplay+episodes[index].title}}</a>
      </li>
      <li :class='$style.single' v-for='(val, index) in episodes.length - 5' v-show='showAll'>
        <a target='_blank' :href='getHref(episodes[index + 5])'>{{episodes[index + 5].episodeDisplay+episodes[index + 5].title}}</a>
      </li>
      <li :class='$style.foldBtn' v-show='!showAll' @click='showAll = true'>??????<i :class='["iconfont", "icon-down", $style.btnArrow]'></i></li>
      <li :class='$style.foldBtn' v-show='showAll' @click='showAll = false'>??????<i :class='["iconfont", "icon-up", $style.btnArrow]'></i></li>
    </template>
  </ul>
</div>
</template>

<script>
export default {
  name: 'e-selection',
  props: {
    bizType: { // ??????????????????
      type: Number,
      required: true
    },
    episodes: { // ?????????
      type: Array,
      required: true
    },
    lineNum: { // ????????????
      type: Number,
      default: 13
    },
    currentEpisode: { // ????????????
      type: Number,
      default: 1
    },
    platform: {
      type: Number,
      default: 0
    },
    videoBaseId: {
      type: String,
      required: true
    }
  },
  watch: {
    currentBatch(batch) {
      const index = batch

      this.getBatch(index)
    }
  },
  data() {
    return {
      showAll: false,
      currentBatch: 1,
      currentBatches: []
    }
  },
  methods: {
    getHref(item) {
      if (this.platform === 0) {
        return `/play.html?videoId=${this.videoBaseId}#${item.videoDetailId}`
      } else {
        return item.sourceUrl
      }
    },
    getBatch(index) {
      const batches = []

      this.episodes.forEach((val, _index) => {
        if (_index >= (index - 1) * 50 && _index < index * 50) {
          batches.push(val)
        }
      })

      this.currentBatches = batches
    }
  },
  created() {
    this.getBatch(this.currentBatch)
  }
}
</script>

<style lang="postcss" module>
.selection {
  .drama {
    transform: translate(-12px, -10px);
    width: 962px;
    .item {
      width: 60px;
      height: 41px;
      margin-left: 12px;
      margin-top: 10px;
      line-height: 41px;
      border-radius: 3px;
      border: 1px solid #DADADA;
      text-align: center;
      font-size: 14px;
      color: #272727;
      cursor: pointer;
      float: left;

      a {
        display: block;
      }

      &:hover {
        background: #FFC31B;
        border: 1px solid #FFC31B;
        color: #fff;
      }
    }
    .foldBtn {
      margin-left: 12px;
      margin-top: 10px;
      width: 134px;
      height: 40px;
      line-height: 40px;
      border-radius: 2px;
      border: 1px solid #DADADA;
      text-align: center;
      font-size: 14px;
      color: #272727;
      cursor: pointer;
      float: left;
      &:hover {
        background: #FFC31B;
        border: 1px solid #FFC31B;
        color: #fff;

        .btnArrow {
          color: #fff;
        }
      }

      & > .btnArrow {
        color: #FFC31B;
        font-size: 12px;
        margin-left: 5px;
      }
    }
  }
  .line {
    position: absolute;
    bottom: 32px;
  }
  .vline {
    position: absolute;
    bottom: 43px;
  }
  .batches {
    margin-top: -10px;
    margin-left: -25px;
    margin-bottom: 10px;
    .batch {
      height: 19px;
      cursor: pointer;
      color: #272727;
      font-size: 14px;
      float: left;
      margin-left: 25px;

      &.highlight {
        color: #FFC31B;
        border-bottom: 2px solid #FFC31B;
      }
    }
  }
  .variety {
    margin-left: -12px;
    .single {
      cursor: pointer;
      width: 306px;
      margin-top: 10px;
      height: 40px;
      line-height: 40px;
      float: left;
      margin-left: 12px;
      border: 1px solid #DDDDDD;
      border-radius: 3px;
      color: #272727;
      font-size: 12px;
      text-align: center;

      a {
        display: block;
        padding: 0 10px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
      &:hover {
        background: #FFC31B;
        border: 1px solid #FFC31B;
        color: #fff;
      }
    }
    .foldBtn {
      margin-left: 10px;
      margin-top: 10px;
      width: 308px;
      height: 40px;
      line-height: 40px;
      border-radius: 2px;
      border: 1px solid #DADADA;
      text-align: center;
      font-size: 14px;
      color: #272727;
      cursor: pointer;
      float: left;
      &:hover {
        background: #FFC31B;
        border: 1px solid #FFC31B;
        color: #fff;

        .btnArrow {
          color: #fff;
        }
      }

      & > .btnArrow {
        color: #FFC31B;
        font-size: 12px;
        margin-left: 5px;
      }
    }
  }
}
</style>



