<template>
<div id="app">
  <e-header></e-header>
  <e-navigation></e-navigation>
  <div :class="$style.searchTip">
    <p v-if="searchApiFlag">?????????<span :class="$style.searchNum">{{searchResCount}}</span>??????<span :class="$style.searchWord">{{searchKey}}</span>???????????????</p>
  </div>
  <div :class="$style.loadingData" v-if="!searchApiFlag">
    <img src="../../assets/common/card-loading.gif" alt="">
  </div>
  <div :class="$style.searchResult">
    <div :class='[$style.searchItem, item.categories == 1 ? $style.movieType : $style.tvType,"u-clearfix"]' v-for='(item, index) in searchRes'>
      <video-item :item='item' :index='index' @change='handlePlatformChange'></video-item>
    </div>
  </div>
  <div :class="$style.noResArea" v-if="searchResCurrCount === 0 && searchApiFlag">
    <img :class="$style.noResIcon" src="../../assets/common/none.png" alt="">
    <p :class="$style.noResContent">?????????????????????????????????</p>
    <div :class="$style.hotwordArea">
      <p :class="$style.hotwordTitle">???????????????????????????</p>
      <div :class="$style.hotwordBox">
        <ul>
          <li v-for="item in hotWord">
            <a :href="`/search.html?q=${item.videoName}`">{{item.videoName}}</a>
          </li>
        </ul>
      </div>
    </div>
  </div>
  <e-paging :class="$style.page" :page-count="pageCount" @page="handlePage" :currentPage="currPage" v-if="pageCount > 1"></e-paging>
  <e-footer></e-footer>
  <e-back-top></e-back-top>
</div>
</template>

<script>
import { storage } from '../../utils'
import videoItem from './item.vue'
import commonMixins from '@/mixins/common'

export default {
  name: 'search',

  components: {
    'video-item': videoItem
  },

  data () {
    return {
      searchKey: '',
      searchRes: [],
      searchResCount: 0, // ????????????
      searchResCurrCount: 0, // ?????????????????????
      currPage: 1,
      pageSize: 20,
      hotWord: [],
      searchApiFlag: false,
    }
  },

  mixins: [commonMixins],

  created() {
    this.searchKey = this.getUrlParam('q') || ''
    if (this.searchKey) {
      this.addToHistory(this.searchKey)
    }
    this.currPage = parseInt(this.getUrlParam('page')) || 1
    this.fetchData(this.searchKey, this.currPage)
  },

  methods: {
    async fetchData(key, page) {
      let res = await this.$fetch('/web/search/selectQueryWord', {
        word: key,
        pageSize: this.pageSize,
        currentPage: page,
      })
      if (res.code === 0) {
        this.searchRes = res.data.videos
        this.searchResCurrCount = res.data.videos.length
        this.searchResCount = res.data.records
        console.log(res)
      }
      this.searchApiFlag = true
      if (this.searchResCurrCount === 0) {
        let res = await this.$fetch('/web/search/hotTop')
        if (res.code === 0) {
          this.hotWord = res.data
        }
      }
    },
    handlePage(page) {
      location.search = '?q=' + this.searchKey + '&page=' + page
    },
    addToHistory(item) {
      const history = item.trim()
      const searchHistory = storage.get('searchHistory') || []
      const index = searchHistory.indexOf(history)

      if (index === -1) {
        if (searchHistory.length < 5) {
          searchHistory.push(history)
        } else {
          searchHistory.shift()
          searchHistory.push(history)
        }
      } else {
        searchHistory.splice(index, 1)
        searchHistory.push(history)
      }

      storage.set('searchHistory', searchHistory)
    },
    handlePlatformChange(data, index) {
      this.searchRes[index].detailCardVos = data
    },
    async addSearchHot(id) {
      await this.$fetch('/web/videoBase/videoBaseInfo', {
        videoBaseId: id,
        searchFlag: 1,
      })
    }
  },

  computed: {
    pageCount() {
      return Math.ceil(this.searchResCount / this.pageSize)
    },
  },
}
</script>

<style lang="postcss" module>
.bodyWrap {
  min-height: calc(100vh - 210px);
}
.searchTip {
  height: 60px;
  background: #EFEFEF;
  p {
    width: 1150px;
    height: 100%;
    line-height: 60px;
    font-size: 15px;
    color: #666666;
    margin: 0 auto;
    span {
      color: #FABE00
    }
  }
}
.searchItem {
  position: relative;
  border-bottom: 1px solid #E5E5E5;
}
.movieType {
  padding-bottom: 20px;
  padding-top: 20px;
}
.tvType {
  padding-bottom: 40px;
  padding-top: 20px;
}
.searchResult {
  width: 1150px;
  margin: 0 auto;
}
.loadingData {
  width: 200px;
  height: 200px;
  margin: 50px auto 200px auto;
  img {
    width: 100%;
    height: 100%;
  }
}
.noResArea {
  width: 1150px;
  margin: 0 auto;
  .noResIcon {
    display: block;
    margin: 80px auto 0 auto;
  }
  .noResContent {
    width: 300px;
    margin: 0 auto 96px auto;
    text-align: center;
    font-size: 20px;
    color: #B8B8B8;
  }
  .hotwordArea {
    .hotwordTitle {
      font-size: 20px;
      color: #B7B7B7;
    }
    .hotwordBox {
      overflow: hidden;
      background: #FAFAFA;
      border-radius: 3px;
      min-height: 40px;
      padding: 10px 0;
      margin-top: 16px;
      li {
        width: 230px;
        height: 20px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        line-height: 16px;
        float: left;
        margin: 10px auto;
        padding-left: 24px;
        box-sizing: border-box;
        a:hover {
          color: #FABE00;
        }
        &::before {
          content: ".";
          color: #FABE00;
          font-weight: bold;
          font-size: 20px;
          position: relative;
          top: -3px;
          margin-right: 10px;
        }
      }
    }
  }
}
.page {
  margin-top: 40px !important;
  margin-bottom: 0px !important;
  padding-bottom: 0 !important;
}
</style>



